.order {
  &__request {
    margin-right: auto;
    margin-bottom: 17px;

    color: #ffffff;

    background-color: #d12136;

    &:hover {
      background-color: #bd0c21;
    }

    &:active {
      color: #dadbdd;
      background-color: #bd0c21;
    }
  }


  &__credit {
    box-sizing: border-box;
    margin-right: auto;

    color: #d12136;

    border: 1px solid #d12136;

    &:hover {
      color: #ffffff;

      background-color: #d12136;
    }

    &:active {
      color: #dadbdd;
      background-color: #bd0c21;
    }
  }
}

@media (max-width: 1023px) {
  .order {
    &__request {
      margin-right: 30px;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 767px) {
  .order {
  }
}
